import css from './List.module.scss'
import classnames from 'classnames'
import Heading, { HeadingTypes } from 'components/Atoms/Heading/Heading'

function List ({
  className,
  list,
  isStripes
}) {
  const {
    heading,
    items
  } = list

  function renderItems (list) {
    return list
      .map((item, index) => (
        <li
          className={css.item}
          key={index}
        >
          { item }
        </li>
      ))
  }

  return (
    <div className={className}>
      {heading &&
        <Heading
          className={css.heading}
          tag={HeadingTypes.H3}
          style={HeadingTypes.H3}
        >
          {heading}
        </Heading>
      }
      <ul
        className={classnames(css.list, {
          [css.listStriped]: isStripes
        })}
      >
        { renderItems(items) }
      </ul>
    </div>
  )
}

export default List
