import Routes from 'Pages/Routes'
import Header from 'components/Organisms/Header/Header'
import useGlobalHeaderPadding from 'hooks/useGlobalHeaderPadding'
import Footer from 'components/Organisms/Footer/Footer'

const DesktopApp = () => {
  const {
    setHeaderRef,
    padding
  } = useGlobalHeaderPadding()

  return (
    <>
      <Header setHeaderRef={setHeaderRef} />
      <main style={{ paddingTop: `${padding}px`, transitionDuration: `0.3s` }}>
        <Routes />
      </main>
      <Footer />
    </>
  )
}

export default DesktopApp
