import css from './Slide.module.scss'
import Heading from 'components/Atoms/Heading/Heading'
import Description from 'components/Atoms/Description/Description'

function Slide ({
  data,
}) {
  const {
    heading,
    description,
    image
  } = data

  return (
    <div className={css.wrapper}>
      <Heading className={css.heading}>
        { heading }
      </Heading>
      <Description className={css.description}>
        { description }
      </Description>
      <img
        src={image}
        className={css.image}
        alt={heading}
      />
    </div>
  )
}

export default Slide
