import classnames from 'classnames'
import css from './SelectorItem.module.scss'

function SelectorItem ({
  onClick,
  tag = `li`,
  isActive,
  children,
}) {
  const TagName = tag

  return (
    <TagName
      className={classnames(css.item, {
        [css.itemActive]: isActive,
      })}
      onClick={onClick}
    >
      { children }
    </TagName>
  )
}

export default SelectorItem
