import css from './MapBannerLayout.module.scss'
import classnames from 'classnames'
import Container from 'components/Templates/Container/Container'
import Heading from 'components/Atoms/Heading/Heading'

function MapBannerLayout ({data}) {
  const {
    map,
    stats
  } = data

  return (
    <section>
      <Container className={css.wrapper}>
        <Heading className={css.heading}>
          Статистика Micasa
        </Heading>
        <div>
          Map
        </div>
        <div className={css.stats}>
          <Heading
            tag={ `span` }
          >
            З нами вже
          </Heading>
          <ul className={css.list}>
            <li
              className={classnames(css.item, css.itemInhabitants)}
              dangerouslySetInnerHTML={{ __html: `${stats.inhabitants}<br />мешканцiв` }}
            />
            <li
              className={classnames(css.item, css.itemBuildings)}
              dangerouslySetInnerHTML={{ __html: `${stats.buildings}<br />будинкiв` }}
            />
          </ul>
          <Heading
            className={css.slogan}
            tag={ `strong` }
          >
            Приєднуйся і ти!
          </Heading>
        </div>
      </Container>
    </section>
  )
}

export default MapBannerLayout
