import {
  SET_LANG,
  TOGGLE_MODAL, UPDATE_SELECTOR
} from 'store/actions/actionTypes'

export const setLang = lang => {
  return {
    type: SET_LANG,
    payload: lang
  }
}

export const toggleModal = (content, options) => {
  return {
    type: TOGGLE_MODAL,
    payload: {
      content,
      options
    }
  }
}

export const updateSelector = value => ({
  type: UPDATE_SELECTOR,
  payload: value
})
