import { useRef } from 'react'
import css from './SliderLayout.module.scss'
import classnames from 'classnames'

// import Swiper core and required modules
import SwiperCore, { Navigation, Pagination } from 'swiper'
import { Swiper, SwiperSlide } from 'swiper/react'
// Import Swiper styles
import 'swiper/swiper.scss'
import 'swiper/components/navigation/navigation.scss'
import 'swiper/components/pagination/pagination.scss'
import Container from 'components/Templates/Container/Container'
import Slide from 'components/Molecules/Slide/Slide'
import IconSliderArrow from 'assets/icons/IconSliderArrow'

SwiperCore.use([Navigation, Pagination])

function SliderLayout ({
  data,
}) {
  const buttonPrevRef = useRef(null)
  const buttonNextRef = useRef(null)

  function renderSlides (data) {
    return data
      .map((slide, index) => (
        <SwiperSlide
          tag={ `li` }
          key={index}
        >
          <Slide data={slide} />
        </SwiperSlide>
      ))
  }

  return (
    <section>
      <Container className={css.wrapper}>
        <Swiper
          slidesPerView={1}
          onSwiper={swiper => {
            swiper.params.navigation.prevEl = buttonPrevRef.current
            swiper.params.navigation.nextEl = buttonNextRef.current
            swiper.navigation.init()
            swiper.navigation.update()
          }}
          navigation={{
            // duplicate prevEl and nextEl to avoid Swiper wrong navigation elements render
            prevEl: buttonPrevRef.current,
            nextEl: buttonNextRef.current,
            disabledClass: css.buttonDisabled
          }}
          pagination={{
            clickable: true,
            bulletClass: css.bullet,
            bulletActiveClass: css.bulletActive
          }}
          wrapperTag={ `ul` }
          resizeObserver={true}
        >
          { renderSlides(data) }
          <button
            className={css.button}
            type="button"
            ref={buttonPrevRef}
          >
            Попередній слайд
            <IconSliderArrow className={css.icon} />
          </button>
          <button
            className={classnames(css.button, css.buttonNext)}
            type="button"
            ref={buttonNextRef}
          >
            Наступний слайд
            <IconSliderArrow className={css.icon} />
          </button>
        </Swiper>
      </Container>
    </section>
  )
}

export default SliderLayout
