import AdvantagesLayout from 'components/Molecules/AdvantagesLayout/AdvantagesLayout'
import { advantagesData } from 'utils/mockData'

function Advantages ({isCorporate}) {
  const {
    corp,
    personal
  } = advantagesData

  return (
    <AdvantagesLayout list={isCorporate ? corp : personal} />
  )
}

export default Advantages
