import css from './RegistrationForm.module.scss'
import Container from 'components/Templates/Container/Container'
import Heading from 'components/Atoms/Heading/Heading'
import IconCityBannerNoColor from 'assets/icons/IconCityBannerNoColor'
import { useForm } from 'react-hook-form'
import Input, { InputHeights } from 'components/Atoms/Input/Input'
import Button, { ButtonHeights } from 'components/Atoms/Button/Button'

function RegistrationForm () {
  const {register, handleSubmit} = useForm()

  function submitForm (data) {
    console.log(data)
  }

  return (
    <section className={css.section}>
      <IconCityBannerNoColor className={css.icon} />
      <Container>
        <Heading
          className={css.heading}
          isLightest
        >
          Залишай заявку на реєстрацію
        </Heading>
        <form onSubmit={handleSubmit(submitForm)}>
          <div className={css.form}>
            <Input
              register={register}
              name={ `company-name` }
              label={ `Назва компанії` }
              height={InputHeights.LARGE}
            />
            <div className={css.doubleColumn}>
              <Input
                register={register}
                name={ `company-phone` }
                label={ `Телефон` }
                height={InputHeights.LARGE}
              />
              <Input
                register={register}
                name={ `company-representative` }
                label={ `Iм'я` }
                height={InputHeights.LARGE}
              />
            </div>
            <Button
              className={css.button}
              height={ButtonHeights.LARGE}
              type="submit"
            >
              Відправити
            </Button>
          </div>
        </form>
      </Container>
    </section>
  )
}

export default RegistrationForm
