import css from './Selector.module.scss'
import classnames from 'classnames'
import SelectorItem from 'components/Atoms/SelectorItem/SelectorItem'

function Selector ({
  className,
  state,
  updateState,
  list
}) {
  function renderItems (list) {
    return list
      .map(({label, isDefault}, index) => (
        <SelectorItem
          onClick={() => updateState(label)}
          isActive={state === label}
          key={index}
        >
          { label }
        </SelectorItem>
      ))
  }

  return (
    <ul className={classnames(css.list, className)}>
      { renderItems(list) }
    </ul>
  )
}

export default Selector
