import css from './HeaderLayout.module.scss'
import classnames from 'classnames'
import Container from 'components/Templates/Container/Container'
import IconLogo from 'assets/icons/IconLogo'
import Selector from 'components/Molecules/Selector/Selector'

function HeaderLayout ({
  className,
  setHeaderRef,
  ...props
}) {
  function handleClickLogo () {
    console.log(`Logo`)
  }

  function handleClickContacts () {
    console.log(`Contacts`)
  }

  return (
    <header
      className={classnames(css.extWrapper, className)}
      ref={setHeaderRef}
    >
      <Container className={classnames(css.wrapper)}>
        <button
          onClick={handleClickLogo}
          className={css.buttonLogo}
          type="button"
        >
          <IconLogo className={css.logo} />
        </button>
        <Selector {...props} />
        <button
          onClick={handleClickContacts}
          className={css.buttonContacts}
          type="button"
        >
          Контакти
        </button>
      </Container>
    </header>
  )
}

export default HeaderLayout
