import { useMemo } from 'react'
import HeaderLayout from 'components/Molecules/HeaderLayout/HeaderLayout'
import useSelectorController from 'hooks/useSelectorController'
import { selectorTabs } from 'utils/mockData'
import useHeaderTypeController from 'hooks/useHeaderTypeController'

function Header ({
  setHeaderRef
}) {
  const selectorList = useMemo(() => Object.values(selectorTabs), []);
  const {...props} = useSelectorController(selectorList)
  const {className} = useHeaderTypeController()

  return (
    <HeaderLayout
      className={className}
      setHeaderRef={setHeaderRef}
      {...props}
    />
  )
}

export default Header
