import { useEffect, useCallback } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { updateSelector } from 'store/actions'

function useSelectorController (list) {
  const value = useSelector(store => store.ui.selectorValue)
  const dispatch = useDispatch()
  // console.log(value)

  const handleUpdateSelector = useCallback((value) => {
    dispatch(updateSelector(value))
  }, [dispatch])

  useEffect(() => {
    const defaultValue = list.find(item => item.isDefault)?.label ?? ``
    handleUpdateSelector(defaultValue)
  }, [list, handleUpdateSelector])

  return {
    state: value,
    updateState: handleUpdateSelector,
    list,
  }
}

export default useSelectorController
