import css from './FeaturesLayout.module.scss'
import UserFeature from 'components/Molecules/UserFeature/UserFeature'
import Container from 'components/Templates/Container/Container'
import Heading from 'components/Atoms/Heading/Heading'

function FeaturesLayout ({list}) {
  function renderItems (list) {
    return list
      .map((item, index) => (
        <UserFeature
          isRTL={index % 2 !== 0}
          key={index}
          {...item}
        />
      ))
  }

  return (
    <section>
      <Container className={css.wrapper}>
        <ul className={css.list}>
          { renderItems(list) }
        </ul>
        <Heading
          className={css.slogan}
          tag={ 'strong' }
        >
          Реєструйся в додатку і розкажи про нас сусіду!
        </Heading>
      </Container>
    </section>
  )
}

export default FeaturesLayout
