import css from './RatesCard.module.scss'
import Heading, { HeadingTypes } from 'components/Atoms/Heading/Heading'
import Description from 'components/Atoms/Description/Description'

function RatesCard ({
  name,
  price,
  description,
  tag = `li`
}) {
  const TagName = tag

  return (
    <TagName className={css.wrapper}>
      <Heading
        className={css.heading}
        style={HeadingTypes.H3}
        tag={HeadingTypes.H3}
      >
        { name }
      </Heading>
      <span className={css.price}>
        { price }
      </span>
      <Description className={css.description}>
        { description }
      </Description>
    </TagName>
  )
}

export default RatesCard
