import css from './AdvantagesLayout.module.scss'
import classnames from 'classnames'
import AdvantageItem from 'components/Molecules/AdvantageItem/AdvantageItem'
import Container from 'components/Templates/Container/Container'
import Heading from 'components/Atoms/Heading/Heading'

function AdvantagesLayout ({
  className,
  list,
}) {
  function renderItems (list) {
    return list
      .map((item, index) => (
        <AdvantageItem
          isImageRight={index % 2 !== 0}
          key={index}
          {...item}
        />
      ))
  }

  return (
    <section>
      <Container className={css.wrapper}>
        <Heading className={css.heading}>
          Переваги
        </Heading>
        <ul className={classnames(css.list, className)}>
          { renderItems(list) }
        </ul>
      </Container>
    </section>
  )
}

export default AdvantagesLayout
