import Banner from 'components/Molecules/Banner/Banner'
import { useSelector } from 'react-redux'
import { selectorTabs } from 'utils/mockData'
import Advantages from 'components/Organisms/Advantages/Advantages'
import Rates from 'components/Organisms/Rates/Rates'
import PromoBanner from 'components/Organisms/PromoBanner/PromoBanner'
import RegistrationForm from 'components/Organisms/RegistrationForm/RegistrationForm'
import Features from 'components/Organisms/Features/Features'
import Slider from 'components/Organisms/Slider/Slider'
import CorpBenefits from 'components/Organisms/CorpBenefits/CorpBenefits'
import MapBanner from 'components/Organisms/MapBanner/MapBanner'

function Home () {
  const selectedTab = useSelector(store => store.ui.selectorValue)
  const isCorporate = selectedTab === selectorTabs.utility.label

  return (
    <>
      <Banner />
      <Advantages isCorporate={isCorporate} />
      {isCorporate && (
        <>
          <Rates />
          <RegistrationForm />
          <Slider />
          <CorpBenefits />
        </>
      )}
      {!isCorporate && (
        <>
          <PromoBanner />
          <Features />
        </>
      )}
      <MapBanner />
    </>
  )
}

export default Home
