import css from './Input.module.scss'
import classnames from 'classnames'

export const InputHeights = {
  STANDARD: `STANDARD`,
  LARGE: `LARGE`
}

function Input ({
  className,
  register,
  name,
  label,
  height = InputHeights.STANDARD,
  ...props
}) {
  return (
    <fieldset className={css.fieldset}>
      {label &&
        <label
          className={css.label}
          htmlFor={ `input-${name}` }
        >
          { label }
        </label>
      }
      <input
        className={classnames(css.input, className, {
          [css.inputLarge]: height === InputHeights.LARGE
        })}
        id={ `input-${name}` }
        {...register(name)}
        {...props}
      />
    </fieldset>
  )
}

export default Input
