import css from './RatesLayout.module.scss'
import classnames from 'classnames'
import RatesCard from 'components/Atoms/RatesCard/RatesCard'
import Container from 'components/Templates/Container/Container'
import Heading from 'components/Atoms/Heading/Heading'

function RatesLayout ({className, list}) {
  function renderItems (items) {
    return items
      .map((item, index) => (
        <RatesCard
          key={index}
          {...item}
        />
      ))
  }

  return (
    <section>
      <Container>
        <Heading className={css.heading}>
          Тарифні плани
        </Heading>
        <ul className={css.list}>
          { renderItems(list) }
        </ul>
      </Container>
    </section>
  )
}

export default RatesLayout
