import css from './PromoBannerLayout.module.scss'
import Container from 'components/Templates/Container/Container'
import Heading from 'components/Atoms/Heading/Heading'
import phonesBg from 'assets/images/phones.png'
import IconAppleStore from 'assets/icons/IconAppleStore'
import IconGooglePlay from 'assets/icons/IconGooglePlay'

function PromoBannerLayout ({
  slogan,
  appStoreURL = '#',
  googleMarketURL = '#'
}) {
  return (
    <section
      className={css.section}
      style={{
        backgroundImage: `url("${phonesBg}")`
      }}
    >
      <Container className={css.wrapper}>
        <Heading
          className={css.heading}
          tag={ `strong` }
          isLightest
        >
          { slogan }
        </Heading>
        <ul className={css.list}>
          <li className={css.item}>
            <a
              className={css.link}
              href={appStoreURL}
            >
              <IconAppleStore className={css.iconApple} />
              Наше додаток в Apple Store
            </a>
          </li>
          <li className={css.item}>
            <a
              className={css.link}
              href={appStoreURL}
            >
              <IconGooglePlay className={css.iconGoogle} />
              Наше додаток в Google Play Market
            </a>
          </li>
        </ul>
      </Container>
    </section>
  )
}

export default PromoBannerLayout
