import { useState, useEffect, useRef, useCallback } from 'react'
import classnames from 'classnames'
import css from 'styles/headerTypes.module.scss'

function useHeaderTypeController () {
  const [className, setClassName] = useState([])
  const scrollTop = useRef(0)

  const watchScrollPosition = useCallback(() => {
    const scrollY = window.scrollY;
    const screenHeight = window.innerHeight|| document.documentElement.clientHeight|| document.body.clientHeight;
    const isScrollingUp = scrollY < scrollTop.current;
    const isScrollingDown = !isScrollingUp;
    const absoluteZone = 0.2 * screenHeight;
    const middleZone = 0.3 * screenHeight;
    const hiddenZone = 0.5 * screenHeight;

    // top zone
    if (isScrollingUp && scrollY <= absoluteZone) {
      removeClassName(css.headerHiddenFixed)
      removeClassName(css.headerHiddenAbsolute)
    }

    // second zone
    if (isScrollingDown && scrollY > absoluteZone && scrollY < hiddenZone) {
      addClassName(css.headerHiddenFixed)
      removeClassName(css.headerHiddenAbsolute)
    }

    if (isScrollingUp && scrollY > absoluteZone && scrollY < middleZone) {
      addClassName(css.headerHiddenAbsolute)
      removeClassName(css.headerHiddenFixed)
      removeClassName(css.headerHiddenFixedTransform)
      removeClassName(css.headerVisibleFixed)
    }

    if (isScrollingUp && scrollY > middleZone && scrollY < hiddenZone) {
      addClassName(css.headerHiddenFixedTransform)
      removeClassName(css.headerHiddenFixed)
      removeClassName(css.headerVisibleFixed)
    }

    //third zone
    if (isScrollingUp && scrollY > hiddenZone) {
      addClassName(css.headerVisibleFixed)
      removeClassName(css.headerHiddenFixed)
    }

    if (isScrollingDown && scrollY > hiddenZone) {
      addClassName(css.headerHiddenFixed)
      removeClassName(css.headerVisibleFixed)
    }

    scrollTop.current = scrollY;

  }, [])

  useEffect(() => {
    document.addEventListener(`scroll`, watchScrollPosition)

    return () => {
      document.removeEventListener(`scroll`, watchScrollPosition)
    }
  }, [watchScrollPosition])

  function removeClassName (className) {
    setClassName(prevState => {
      const state = prevState.slice()
      const targetIndex = state.findIndex(item => item === className)

      if (targetIndex >= 0) {
        return [].concat(state.slice(0, targetIndex), state.slice(targetIndex + 1))
      }

      return state
    })
  }

  function addClassName (className) {
    setClassName(prevState => {
      const state = prevState.slice()

      if (!state.includes(className)) {
        state.push(className)
      }

      return state
    })
  }

  return {
    className: classnames(...className)
  }
}

export default useHeaderTypeController
