import css from './ContactForm.module.scss'
import { useForm } from 'react-hook-form'
import Heading, { HeadingTypes } from 'components/Atoms/Heading/Heading'
import Input from 'components/Atoms/Input/Input'
import Button from 'components/Atoms/Button/Button'

function ContactForm () {
  const {register, handleSubmit} = useForm()

  function submitForm (data) {
    console.log(data)
  }

  return (
    <form onSubmit={handleSubmit(submitForm)}>
      <Heading
        style={HeadingTypes.H3}
        tag={HeadingTypes.H3}
        isLightest
      >
        { `Залишились запитання?<br />Зв’яжись з нами` }
      </Heading>
      <div className={css.form}>
        <Input register={register} name="name" placeholder={ `Iм'я` } />
        <Input register={register} name="email" placeholder={ `Email` } />
        <Input register={register} name="content" placeholder={ `Запитання` } />
      </div>
      <Button
        className={css.button}
        type="submit"
      >
        Надіслати
      </Button>
    </form>
  )
}

export default ContactForm
