import css from 'components/Organisms/Footer/Footer.module.scss'
import Container from 'components/Templates/Container/Container'
import IconLogo from 'assets/icons/IconLogo'
import Heading, { HeadingTypes } from 'components/Atoms/Heading/Heading'
import ContactForm from 'components/Organisms/ContactForm/ContactForm'
import { promoBannerData } from 'utils/mockData'
import IconAppleStore from 'assets/icons/IconAppleStore'
import IconGooglePlay from 'assets/icons/IconGooglePlay'

function Footer () {
  return (
    <footer className={css.footer}>
      <Container className={css.wrapper}>
        <div className={css.contacts}>
          <IconLogo className={css.logo} />
          <Heading
            className={css.heading}
            tag={HeadingTypes.H2}
            style={HeadingTypes.H3}
            isLightest
          >
            Контакти
          </Heading>
          <ul className={css.list}>
            <li className={css.item}>
              <a
                href="mailto:support@micasa.com.ua"
                className={css.link}
              >
                support@micasa.com.ua
              </a>
            </li>
            <li className={css.item}>
              <a
                href="tel:+380980909"
                className={css.link}
              >
                +38 098 09 09
              </a>
            </li>
          </ul>
        </div>
        <ContactForm />
        <div className={css.apps}>
          <Heading
            tag={HeadingTypes.H2}
            style={HeadingTypes.H3}
            isLightest
          >
            Додаток для мешканців
          </Heading>
          <a
            href={promoBannerData.appStoreURL}
            className={css.store}
          >
            <IconAppleStore className={css.icon} />
            Наше додаток в Apple Store
          </a>
          <a
            href={promoBannerData.googleMarketURL}
            className={css.store}
          >
            <IconGooglePlay className={css.icon} />
            Наше додаток в Google Play Market
          </a>
        </div>
      </Container>
    </footer>
  )
}

export default Footer
